import styled from "styled-components";
import { AiFillBell, AiOutlineClose } from "react-icons/ai";
import { useRecoilState } from "recoil";
import { alarmDatasetState } from "../../store/atom";
import axios from "axios";

function AlarmCard({ data }) {
  
  const [dataset, setDataset] = useRecoilState(alarmDatasetState); //   

  //   
  const handleRemove = async ({ id }) => {
    let filtered = dataset.filter((element) => element !== data);

    await axios.delete(`${process.env.REACT_APP_API_URL}/alarmData/${id}`).then(
      //       
      setDataset(filtered)
    );
  };
  
  //  
  const dDay = (data) => {
    let today = new Date();
    let dday = new Date(
      Number(data.year),
      Number(data.month)-1,
      Number(data.date)
    );
    let gap = dday.getTime() - today.getTime();
    let result = Math.ceil(gap / (1000 * 60* 60* 24));
    return result;
  }

  return (
    <AlarmItems>
      <AlarmItemsContents>
        <AiFillBell size="15" color="#98a8f0" />
        {dDay(data) === 0 ? (
          <AlarmText>'{data.alarmContents}'  !</AlarmText>
        ) : dDay(data) > 0 ? (
          <AlarmText>
            '{data.alarmContents}' {dDay(data)} .
          </AlarmText>
        ) : (
          <AlarmText>
            '{data.alarmContents}'   .
          </AlarmText>
        )}
      </AlarmItemsContents>
      <AlarmItemsclose type="button" onClick={()=>{handleRemove({id: data.id})}}>
        <AiOutlineClose />
      </AlarmItemsclose>
    </AlarmItems>
  );
}


// CSS
const AlarmItemsclose = styled.div`
  display: flex;
  position: relative;
  right: -20px;
  width: 10px;
  transition: 0.3s;
  cursor: pointer;
`;
const AlarmItems = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  width: 350px;
  padding: 3px 8px;
  margin-top: 10px;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  overflow: hidden;
  &:hover ${AlarmItemsclose} {
    transition: 0.3s;
    right: 10px;
  }
  @media screen and (min-width: 1200px) {
    margin-right: 10px;
    flex-wrap: wrap;
    padding: 5px 8px;
  }
`;

const AlarmItemsContents = styled.div`
  display: flex;
  @media screen and (min-width: 1200px) {
    width: 90%;
  }
`;

const AlarmText = styled.div`
  margin-left: 5px;
  font-size: 13px;
`;

export default AlarmCard;
