import axios from "axios";
import { useState, useEffect } from "react";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { alarmDatasetState, alarmModalState } from "../../store/atom";
import GlobalModal from "./GlobalModal";


function AlarmModal() {
  const [deposit, setDeposit] = useState(true); //  
  const [alarmOpen, setAlarmOpen] = useRecoilState(alarmModalState); //    
  const [dataset, setDataset] = useRecoilState(alarmDatasetState); //   

  //  id 
  const uuidv4 = () => {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  };

  //   
  const [inputs, setInputs] = useState({
    accountType: "deposit",
    year: "",
    month: "",
    date: "",
    alarmContents: "",
    price: 0,
    id: uuidv4(),
  });

  const { year, month, date, alarmContents, price } = inputs;


  // input     
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };

  //    
  const handleDeposit = () => {
    setDeposit(!deposit);
  };

  //      
  const handleSubmit = async () => {
    if (year.length < 1) {
      return alert(" ");
    } else if (month.length < 1) {
      return alert(" ");
    } else if (date.length < 1) {
      return alert(" ");
    } else if (alarmContents.length < 1) {
      return alert(" ");
    } else if (price < 1) {
      return alert(" ");
    }

    //  2  
    if (dataset.length < 2) {
      await axios
        .post(`${process.env.REACT_APP_API_URL}/alarmData`, inputs)
        .then((res) => {
          //       
          setDataset([inputs, ...dataset]);

          // input  
          setInputs({
            accountType: "Deposit",
            year: "",
            month: "",
            date: "",
            accountContents: "",
            price: 0,
          });
          setDeposit(true);
          setAlarmOpen(false);
        });
    } else {
      alert("  2 .");
    }
  };

  //      
  const handleCancel = () => {
    if (alarmOpen === true) {
      setAlarmOpen(false);
    }
  };

  useEffect(() => {
    //      
    if (deposit === true) {
      setInputs({
        ...inputs,
        ["accountType"]: "Deposit",
      });
    } else {
      setInputs({
        ...inputs,
        ["accountType"]: "Withdraw",
      });
    }
  }, [deposit]);

  return (
    <GlobalModal title=" " icon="alarm">
      <form onSubmit={handleSubmit}>
        <Label></Label>
        <DateInputBox>
          <DateInput
            placeholder="2023"
            name="year"
            value={year}
            onChange={onChange}
            type="number"
            min="2015"
            max="2100"
          />
          <DateInput
            placeholder="1"
            name="month"
            value={month}
            onChange={onChange}
            type="number"
            min="1"
            max="12"
          />
          <DateInput
            placeholder="1"
            name="date"
            value={date}
            onChange={onChange}
            type="number"
            min="1"
            max="31"
          />
        </DateInputBox>
        <Label></Label>
        {deposit === true ? (
          <ContentButtonBox>
            <ContentButton onClick={handleDeposit} color="#D9D9D9" disabled>
              
            </ContentButton>
            <ContentButton onClick={handleDeposit}></ContentButton>
          </ContentButtonBox>
        ) : (
          <ContentButtonBox>
            <ContentButton onClick={handleDeposit}></ContentButton>
            <ContentButton onClick={handleDeposit} color="#D9D9D9" disabled>
              
            </ContentButton>
          </ContentButtonBox>
        )}
        <Input
          placeholder="  1"
          marginBottom="10px"
          name="alarmContents"
          value={alarmContents}
          onChange={onChange}
        />
        <br />
        <Label></Label>
        <Input
          type="number"
          placeholder="15,000"
          marginTop="5px"
          name="price"
          value={price === 0 ? null : price}
          onChange={onChange}
        />
      </form>
      <ModalButtonContainer>
        <ModalButton
          marginRight="10px"
          backgroundColor="#fff"
          onClick={handleSubmit}
        >
          
        </ModalButton>
        <ModalButton
          marginRight="10px"
          backgroundColor="#d9d9d9"
          onClick={handleCancel}
        >
          
        </ModalButton>
      </ModalButtonContainer>
    </GlobalModal>
  );
}


// CSS
const Label = styled.label`
  color: #fff;
  font-weight: 700;
  font-size: 14px;
`;
const DateInputBox = styled.div`
  display: flex;
  justify-content: space-between;
  width: 340px;
  margin: 5px 0 10px 0;
`;
const DateInput = styled.input`
  width: 28%;
  height: 30px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
`;
const ContentButtonBox = styled.div`
  display: flex;
  justify-content: space-between;
`;
const ContentButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48%;
  height: 40px;
  margin: 5px 0 10px 0;
  border: none;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  background-color: ${(props) => props.color || "#fff"};
  cursor: pointer;
  &:hover {
    background-color: #d9d9d9;
  }
`;
const Input = styled.input`
  width: 330px;
  height: 40px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
  margin-top: ${(props) => props.marginTop || 0};
  margin-bottom: ${(props) => props.marginBottom || 0};
`;
const ModalButtonContainer = styled.div`
  margin-top: 30px;
  text-align: center;
`;
const ModalButton = styled.button`
  width: 80px;
  height: 35px;
  margin-right: ${(props) => props.marginRight};
  background-color: ${(props) => props.backgroundColor};
  border: none;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  cursor: pointer;
  &:hover {
    background-color: grey;
  }
`;

export default AlarmModal;
