// react   

import axios from "axios";
import styled from "styled-components";
import usePromise from "../../hooks/usePromise";
import NewsItems from "./NewsItems";

function NewsContainer() {
  // promise response      -> usePromise 
  const [loading, response, error] = usePromise(() => {
    return axios.get(
      `https://newsapi.org/v2/top-headlines?country=kr&category=business&apiKey=${process.env.REACT_APP_NEWS_API_KEY}`
    );
  }, []);

  //  
  if (loading) {
    return <Container>Loading...</Container>;
  }
  //  response    
  if (!response) {
    return null;
  }

  //   
  if (error) {
    return <Container> NewsApi  .</Container>;
  }

  // response   
  const { articles } = response.data;
  return (
    <Container>
      <Title>  </Title>
      <Contents>
        {articles.map((article) => (
          <NewsItems key={article.url} article={article} />
        ))}
      </Contents>
    </Container>
  );
}

//CSS
const Container = styled.div`
  height: 200px;
  width: 340px;
  padding: 15px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: rgb(60 64 67 / 30%) 0px 1px 2px 0px,
    rgb(60 64 67 / 15%) 0px 2px 6px 2px;
  margin-top: 30px;
  overflow: scroll;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    width: 8px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #ab9df0;
    border-radius: 10px;
    box-shadow: inset 0px 0px 1px white;
  }
  @media screen and (min-width: 1200px) {
    margin-top: 35px;
    margin-left: 30px;
    height: 222px;
    width: 30%;
    padding: 15px 5px 15px 15px;
  }
`;

const Title = styled.div`
  color: #2b2b2b;
  font-weight: 700;
`;

const Contents = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;

export default NewsContainer;
