import { useState } from "react";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import GlobalModal from "./GlobalModal";
import { goalModalState, goalDatasetState } from "../../store/atom";
import axios from "axios";


function GoalModal() {
  //     
  const [goalOpen, setGoalOpen] = useRecoilState(goalModalState);
  //   
  const [dataset, setDataset] = useRecoilState(goalDatasetState);

  //  id 
  const uuidv4 = () => {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  };

  //   
  const [inputs, setInputs] = useState({
    startYear: "",
    startMonth: "",
    startDate: "",
    endYear: "",
    endMonth: "",
    endDate: "",
    goalContents: "",
    price: 0,
    id: uuidv4(),
  });

  const {
    startYear,
    startMonth,
    startDate,
    endYear,
    endMonth,
    endDate,
    goalContents,
    price,
  } = inputs;

  //    
  const handleCancel = () => {
    if (goalOpen === true) {
      setGoalOpen(false);
    }
  };

  // input     
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };

  //      
  const handleSubmit = async () => {
    //  
    if (startYear.length < 1) {
      return alert("  ");
    } else if (startMonth.length < 1) {
      return alert("  ");
    } else if (startDate.length < 1) {
      return alert("  ");
    } else if (endYear.length < 1) {
      return alert("  ");
    } else if (endMonth.length < 1) {
      return alert("  ");
    } else if (endDate.length < 1) {
      return alert("  ");
    } else if (goalContents.length < 1) {
      return alert(" ");
    } else if (price < 1) {
      return alert(" ");
    }

    if (dataset.length < 2) {
      await axios.post(`${process.env.REACT_APP_API_URL}/goalData`, inputs).then((res) => {
        //       
        setDataset([inputs, ...dataset]);

        // input  
        setInputs({
          startYear: "",
          startMonth: "",
          startDate: "",
          endYear: "",
          endMonth: "",
          endDate: "",
          goalContents: "",
          price: 0,
        });
        setGoalOpen(false);
      });
    } else {
      alert("   2 .");
    }
  };

  return (
    <GlobalModal title=" " icon="goal">
      <form onSubmit={handleSubmit}>
        <Label> </Label>
        <DateInputBox>
          <DateInput
            placeholder="2023"
            name="startYear"
            value={startYear}
            onChange={onChange}
            type="number"
            min="2015"
            max="2100"
          />
          <DateInput
            placeholder="1"
            name="startMonth"
            value={startMonth}
            onChange={onChange}
            type="number"
            min="1"
            max="12"
          />
          <DateInput
            placeholder="1"
            name="startDate"
            value={startDate}
            onChange={onChange}
            type="number"
            min="1"
            max="31"
          />
        </DateInputBox>
        <Label> </Label>
        <DateInputBox>
          <DateInput
            placeholder="2023"
            name="endYear"
            value={endYear}
            onChange={onChange}
            type="number"
            min="2015"
            max="2100"
          />
          <DateInput
            placeholder="2"
            name="endMonth"
            value={endMonth}
            onChange={onChange}
            type="number"
            min="1"
            max="12"
          />
          <DateInput
            placeholder="1"
            name="endDate"
            value={endDate}
            onChange={onChange}
            type="number"
            min="1"
            max="31"
          />
        </DateInputBox>
        <Label> </Label>
        <Input
          placeholder="  1"
          marginTop="5px"
          marginBottom="10px"
          name="goalContents"
          value={goalContents}
          onChange={onChange}
        />
        <br />
        <Label></Label>
        <Input
          placeholder="15,000"
          marginTop="5px"
          name="price"
          value={price === 0 ? null : price}
          onChange={onChange}
        />
      </form>
      <ModalButtonContainer>
        <ModalButton
          marginRight="10px"
          backgroundColor="#fff"
          onClick={handleSubmit}
        >
          
        </ModalButton>
        <ModalButton
          marginRight="10px"
          backgroundColor="#d9d9d9"
          onClick={handleCancel}
        >
          
        </ModalButton>
      </ModalButtonContainer>
    </GlobalModal>
  );
}


// CSS
const Label = styled.label`
  color: #fff;
  font-weight: 700;
  font-size: 14px;
`;
const DateInputBox = styled.div`
  display: flex;
  justify-content: space-between;
  width: 340px;
  margin: 5px 0 10px 0;
`;
const DateInput = styled.input`
  width: 28%;
  height: 30px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
`;
const Input = styled.input`
  width: 330px;
  height: 40px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
  margin-top: ${(props) => props.marginTop || 0};
  margin-bottom: ${(props) => props.marginBottom || 0};
`;
const ModalButtonContainer = styled.div`
  margin-top: 30px;
  text-align: center;
`;
export const ModalButton = styled.button`
  width: 80px;
  height: 35px;
  margin-right: ${(props) => props.marginRight};
  background-color: ${(props) => props.backgroundColor};
  border: none;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  cursor: pointer;
  &:hover {
    background-color: grey;
  }
`;

export default GoalModal;
