import axios from "axios";
import { useEffect, useState } from "react";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import styled from "styled-components";
import {
  datasetState,
  categoryDatasetState,
  categoryModalState,
  categoryChartDatasetState,
} from "../store/atom";


function CreateAccountHistory({onSubmit}) {
  const [open, setOpen] = useState(false); //   
  const [deposit, setDeposit] = useState(true); //  
  const openCategory = useSetRecoilState(categoryModalState); //   
  const [categoryChartData, setCategoryChartData] = useRecoilState(
    categoryChartDatasetState
  ); 

  //   
  const [dataset, setDataset] = useRecoilState(datasetState);
  const categoryList = useRecoilValue(categoryDatasetState);

  function uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  }

  //   
  const [inputs, setInputs] = useState({
    accountType: "deposit",
    year: "",
    month: "",
    date: "",
    accountContents: "",
    price: 0,
    category: "",
    id: uuidv4(),
  });

  const { year, month, date, accountContents, price } = inputs;

  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };

  //   
  const handleCategory = (e) => {
    const { value } = e.target;
    setInputs({
      ...inputs,
      ["category"]: value,
    });
  };

  //     
  const handleCategoryModal = () => {
    console.log("enter");
    openCategory(true);
  };

  //    
  const handleDeposit = () => {
    setDeposit(!deposit);
  };

  //      
  const handleAddHistory = () => {
    setOpen(true);
  };

  //      
  const handleSubmit = async () => {
    //  
    if (year.length < 1) {
      return alert(" ");
    } else if (month.length < 1) {
      return alert(" ");
    } else if (date.length < 1) {
      return alert(" ");
    } else if (accountContents.length < 1) {
      return alert(" ");
    } else if (price < 1) {
      return alert(" ");
    }

    await axios
      .post(`${process.env.REACT_APP_API_URL}/accountHistoryData`, inputs)
      .then((res) => {
        //       
        setDataset([inputs, ...dataset]);
        // input  
        setInputs({
          accountType: "Deposit",
          year: "",
          month: "",
          date: "",
          category: "",
          accountContents: "",
          price: 0,
        });
        setDeposit(true);
        setOpen(false);

        let index = categoryChartData.findIndex(
          (e) => e.category === inputs.category
        );

        let price = categoryChartData[index].total + Number(inputs.price);
        let category = inputs.category;

        //    
        let filtered = categoryChartData.filter(
          (element) => element.category !== inputs.category
        );
        filtered.push({ category: category, total: price });
        setCategoryChartData(filtered);

        axios.put(
          `${process.env.REACT_APP_API_URL}/categoryChartData/${index + 1}`,
          { category: category, total: price }
        );
      })
      .then((res) => {
        console.log("");
      });
  };

  //      
  const handleCancel = () => {
    setOpen(false);
  };

  useEffect(() => {
    //      
    if (deposit === true) {
      setInputs({
        ...inputs,
        ["accountType"]: "Deposit",
      });
    } else {
      setInputs({
        ...inputs,
        ["accountType"]: "Withdraw",
      });
    }
  }, [deposit]);

  return (
    <div>
      {open === true ? (
        <div>
          <CreateAccountHistoryContainer>
            <CreateAccountHistoryHeader>
              <CreateAccountHistoryTitle>
                  
              </CreateAccountHistoryTitle>
              <SubmitButton backgroundColor="#fff" onClick={handleSubmit}>
                
              </SubmitButton>
            </CreateAccountHistoryHeader>
            <form onSubmit={onSubmit}>
              <Label></Label>
              <DateInputBox>
                <DateInput
                  placeholder="2023"
                  name="year"
                  value={year}
                  onChange={onChange}
                  type="number"
                  min="2015"
                  max="2100"
                />
                <DateInput
                  placeholder="1"
                  name="month"
                  value={month}
                  onChange={onChange}
                  type="number"
                  min="1"
                  max="12"
                />
                <DateInput
                  placeholder="1"
                  name="date"
                  value={date}
                  onChange={onChange}
                  type="number"
                  min="1"
                  max="31"
                />
              </DateInputBox>
              <Label>
                
                <CategoryButton
                  type="button"
                  onClick={() => {
                    handleCategoryModal();
                  }}
                >
                  +
                </CategoryButton>
              </Label>
              <Select onChange={handleCategory}>
                <option>-- --</option>
                {categoryList.map((item) => (
                  <option value={item.label} key={item.id}>
                    {item.label}
                  </option>
                ))}
              </Select>
              <Label></Label>
              {deposit === true ? (
                <ContentButtonBox>
                  <ContentButton
                    onClick={handleDeposit}
                    color="#d3d3d3"
                    disabled
                  >
                    
                  </ContentButton>
                  <ContentButton onClick={handleDeposit}></ContentButton>
                </ContentButtonBox>
              ) : (
                <ContentButtonBox>
                  <ContentButton onClick={handleDeposit}></ContentButton>
                  <ContentButton
                    onClick={handleDeposit}
                    color="#d3d3d3"
                    disabled
                  >
                    
                  </ContentButton>
                </ContentButtonBox>
              )}
              <Input
                placeholder="  1"
                marginBottom="10px"
                name="accountContents"
                value={accountContents}
                onChange={onChange}
              />
              <br />
              <Label></Label>
              <Input
                type="number"
                placeholder="15,000"
                marginTop="5px"
                name="price"
                value={price === 0 ? undefined : price}
                onChange={onChange}
              />
            </form>
          </CreateAccountHistoryContainer>
          <ButtonCotainer>
            <Button backgroundColor="#fff" onClick={handleSubmit}>
              
            </Button>
            <Button
              color="#979694"
              backgroundColor="#d9d9d9"
              onClick={handleCancel}
            >
              
            </Button>
          </ButtonCotainer>
        </div>
      ) : (
        <>
          <Button onClick={handleAddHistory}>  </Button>
          <CreateAccountHistoryContainer display="none">
            <CreateAccountHistoryHeader>
              <CreateAccountHistoryTitle>
                  
              </CreateAccountHistoryTitle>
              <SubmitButton backgroundColor="#fff" onClick={handleSubmit}>
                
              </SubmitButton>
            </CreateAccountHistoryHeader>
            <form onSubmit={onSubmit}>
              <Label></Label>
              <DateInputBox>
                <DateInput
                  placeholder="2023"
                  name="year"
                  value={year}
                  onChange={onChange}
                  type="number"
                  min="2015"
                  max="2100"
                />
                <DateInput
                  placeholder="1"
                  name="month"
                  value={month}
                  onChange={onChange}
                  type="number"
                  min="1"
                  max="12"
                />
                <DateInput
                  placeholder="1"
                  name="date"
                  value={date}
                  onChange={onChange}
                  type="number"
                  min="1"
                  max="31"
                />
              </DateInputBox>
              <Label>
                
                <CategoryButton
                  type="button"
                  onClick={() => {
                    handleCategoryModal();
                  }}
                >
                  +
                </CategoryButton>
              </Label>
              <Select onChange={handleCategory}>
                <option>-- --</option>
                {categoryList.map((item) => (
                  <option value={item.label} key={item.id}>
                    {item.label}
                  </option>
                ))}
              </Select>
              <Label></Label>
              {deposit === true ? (
                <ContentButtonBox>
                  <ContentButton
                    onClick={handleDeposit}
                    color="#d3d3d3"
                    disabled
                  >
                    
                  </ContentButton>
                  <ContentButton onClick={handleDeposit}></ContentButton>
                </ContentButtonBox>
              ) : (
                <ContentButtonBox>
                  <ContentButton onClick={handleDeposit}></ContentButton>
                  <ContentButton
                    onClick={handleDeposit}
                    color="#d3d3d3"
                    disabled
                  >
                    
                  </ContentButton>
                </ContentButtonBox>
              )}
              <Input
                placeholder="  1"
                marginBottom="10px"
                name="accountContents"
                value={accountContents}
                onChange={onChange}
              />
              <br />
              <Label></Label>
              <Input
                type="number"
                placeholder="15,000"
                marginTop="5px"
                name="price"
                value={price === 0 ? undefined : price}
                onChange={onChange}
              />
            </form>
          </CreateAccountHistoryContainer>
        </>
      )}
    </div>
  );
}

// CSS
const CreateAccountHistoryContainer = styled.div`
  width: 340px;
  padding: 15px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: linear-gradient(45deg, #98a8f0, #b09bf0);
  overflow: scroll;
  overflow-x: hidden;

  @media screen and (min-width: 1200px) {
    width: 23vw;
    height: 300px;
    margin-left: 30px;
    margin-bottom: 1px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  }

  @media screen and (min-width: 1600px) {
    width: 350px;
  }
  @media screen and (max-width: 1200px) {
    display: ${(props) => props.display || "block"};
  }

  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: inset 0px 0px 1px white;
  }
`;

const CreateAccountHistoryHeader = styled.div`
  display: flex;
  justify-content: space-between;
`

const CreateAccountHistoryTitle = styled.div`
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  margin-bottom: 10px;
`;

//    
const ButtonCotainer = styled.div`
  @media screen and (min-width: 1200px) {
    display: flex;
    
  }
`;

const Button = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 370px;
  height: 40px;
  margin: 10px auto;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  color: ${(props) => props.color || "#000"};
  background-color: ${(props) => props.backgroundColor};
  cursor: pointer;
  &:hover {
    background-color: #d3d3d3;
    color: #979694;
  }
  font-weight: 700;

  @media screen and (min-width: 1200px) {
      display: none;
  }
`;    

// wide  ,  
const SubmitButton = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  font-size: 13px;
  font-weight: 700;
  color: #000;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  cursor: pointer;
  &:hover {
    background-color: #d3d3d3;
    color: #979694;
  }
  @media screen and (max-width: 1200px) {
    display: none;
  }
`;

// form css
const Label = styled.label`
  color: #fff;
  font-weight: 700;
  font-size: 14px;
`;

const DateInputBox = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 5px 0 10px 0;
`;

const DateInput = styled.input`
  width: 28%;
  height: 30px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
`;

const Input = styled.input`
  width: 97%;
  height: 40px;
  border-radius: 10px;
  border: none;
  padding-left: 10px;
  margin-top: ${(props) => props.marginTop || 0};
  margin-bottom: ${(props) => props.marginBottom || 0};
`;


//   CSS
const ContentButtonBox = styled.div`
  display: flex;
  justify-content: space-between;
`;

const ContentButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48%;
  height: 40px;
  margin: 5px 0 10px 0;
  border: none;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  background-color: ${(props) => props.color || "#fff"};
  font-weight: 700;
  cursor: pointer;
  &:hover {
    background-color: #d3d3d3;
  }
`;

//  CSS
//  : https://codepen.io/daleseo/pen/VwaPzMy
const Select = styled.select`

  width: 100%;

  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;

  color: #444;
  background-color: #fff;

  padding: 0.6em 1.4em 0.5em 0.8em;
  margin-bottom: 10px;

  border: 1px solid #aaa;
  border-radius: 0.5em;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);

  &:hover {
    border-color: #888;
  }

  &:focus {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
  }

  &:disabled {
    opacity: 0.5;
  }
`;

const CategoryButton = styled.button`
  margin-bottom: 5px;
  background-color: #fff;
  border-radius: 15px;
  border: none;
  cursor: pointer;
`;

export default CreateAccountHistory;
