import { useRecoilState, useSetRecoilState } from "recoil";
import { categoryDatasetState, categoryModalState } from "../../store/atom";
import { Header, HeaderTitle, Modal, ModalContainer, ModalContents } from "./GlobalModal";
import styled from "styled-components";
import { useState } from "react";
import axios from "axios";
import { ModalButton } from "./GoalModal";

function CategoryModal() {
  //    
  const categoryOpen = useSetRecoilState(categoryModalState);
  const [dataset, setDataset] = useRecoilState(categoryDatasetState); //   

  //   
  const [inputs, setInputs] = useState({});

  //     
  const addCategory = async () => {
    await axios
      .post(`${process.env.REACT_APP_API_URL}/categoryData`, inputs)
      .then((res) => {
        //       
        setDataset([inputs, ...dataset]);
        categoryOpen(false);
      });
  };

  //  input    
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({ [name]: value });
  };

  //   
  const handleCancel = () =>{
    categoryOpen(false);
  }

  return (
    <Modal display={categoryOpen ? "flex" : "none"}>
      <ModalContainer height="150px">
        <Header>
          <HeaderTitle> </HeaderTitle>
        </Header>
        <ModalContents>
          <Input
            name="label"
            onChange={onChange}
            placeholder="   "
          ></Input>
        </ModalContents>
        <ButtonContainer>
          <ModalButton backgroundColor="#fff" onClick={addCategory}>
            
          </ModalButton>
          <ModalButton backgroundColor="#d9d9d9" onClick={handleCancel}></ModalButton>
        </ButtonContainer>
      </ModalContainer>
    </Modal>
  );
}

const Input = styled.input`
    width: 95%;
    height: 30px;
    border: none;
    border-radius: 5px;
    padding-left: 15px;
`

const ButtonContainer = styled.div`
    display: flex;
    justify-content: center;
    margin-top: 15px;

    //  
    button {
        margin-right: 10px;
    }
`

export default CategoryModal;